div.holder {
  background-color: #484a49;
  vertical-align: middle;
}

hr.divider {
  border-width: 5px;
  border-style: solid;
  color: white;
}

#child {
  -webkit-transform: scale(0.9);
  -moz-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
}

.favorite-button {
  width: 40px;
  height: auto;
  position: absolute;
  float: left;
  margin-left: 15px;
  margin-top: 10px;
}
